.callButton {
  background-color: rgb(106, 168, 213);
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 50%; /* Make the border-radius 50% to create a circular button */
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
  margin-top: 10%;
}

.callButton:hover {
  background-color: #10aadd;
}

