.hotels {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px; /* Adding some padding to the container */
}

.hotels .hotelTitle {
  font-family: 'Times New Roman', Times, serif;
  font-size: 25px;
  text-align: center; /* Centering the text */
}

.hotelsList {
  display: grid;
  grid-template-columns: 1fr; /* Single column layout for mobile */
  gap: 20px; /* Adding gap between items */
}

.hotelsList > div{
  width : 500px;
}

.hotelItem {
  border-radius: 15px;
  width: 100%; /* Full width for mobile */
  height: auto; /* Adjusting height based on content */
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease-in, transform 0.3s ease-in; /* Combined transitions */
}

.hotelItem:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  transform: scale(1.02); /* Slight scale on hover */
  cursor: pointer;
}

.hotelItem div {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 200px;
  max-height: 200px; /* Adding a max height */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hotelItem h1,
.hotelItem p {
  margin: 10px; /* Adjusting margin */
  text-align: center; /* Centering text */
}

/* Media queries for different screen sizes */

/* For tablets */
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .hotelsList {
    grid-template-columns: repeat(2, 1fr); /* Two columns layout */
  }
}

/* For smaller screens like mobile phones */
@media only screen and (max-width: 767px) {
  .hotelItem {
    margin: 10px 0; /* Smaller margin for better spacing */
  }
}
